@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@100..900&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: 'Roboto Slab', sans-serif;
}

section{
  min-height: 100vh;
  display: flex;
  justify-content: center;
  background: #0e192b;
  color: white;
}

section:nth-child(odd){
  background: #142745;
}


.home div{
  width: 50%;
  display: flex;
  align-items: center;
}

.home-pic img{
  height: 75vh;
  margin-top: 32px;
}

.home-info div{
  align-items: flex-start;
  width: 90%;
}

.home-info h5{
  background-color: #1c2b4d;
  letter-spacing: 1px;
}

.home-text span{
  font-size: 1.5rem;
  line-height: 2rem;
  
}

.typed-cursor {
  font-size: 1.5rem;
}

.cv-btn {
  background-color: #357bf7;
}


.social-media{
  display: flex;
  width: 50%;
}

.social-media a:hover{
  transform: scale(1.1);
  transition: 0.5s;
}

.social-media a{
  transition: 0.5s;
}


.social-media img{
  width: 2.5rem;
  border: 2px solid white;
  border-radius: 50%;
  padding: 4px;
  transition: 0.5s;
}

.social-media img:hover{
  box-shadow: 0 0 8px 0 white;
}

@media only screen and (max-width:1096px) {
  .home-info h1{
    font-size: 48px;
  }

  .home-info {
    padding-left: 1rem;
  }

}

@media only screen and (max-width: 1024px) and (max-height:1400px){
  .home {
    flex-direction: column;
    align-items: center;
    gap: 1.25rem;
    padding-top: 5rem;
    padding-bottom: 5rem;
  }
  .home div{
    width: 100%;
    align-items: center;
  }
  .home-text{
    justify-content: center;
  }
  .home-text span{
    text-align: center;
    font-size: 24px;
  }

  .home h1{
    text-align: center;
  }

  .home-pic img{
    height: 45vh;
  }

  .social-media{
    width: 100%;
    justify-content: center;
  }
}

