@media only screen and (max-width:480px) {
  .home-pic img{
    height: 360px;
  }

  .home-info{
    gap: 0.5rem;
  }

  .home-info h5{
    font-size: 8px;
    line-height: 16px;
  }

  .home-info h1{
    font-size: 38px;
  }

  .home-text span{
    font-size: 18px;
  }
  
}


